<template>
  <!---->
  <md-card class="md-elevation-5 my-hover">
    <md-card-content>
      <!---->
      <img src="/static/zc.gif"/>

      <!---->
      <p style="color: blue;font-size: 20px;text-align: right"></p>

      <!---->
      <md-field :class="isPhone(form.phone_number)">
        <label></label>
        <md-input type="number" v-model="form.phone_number">
        </md-input>
        <md-button class="my-raised my-hover"></md-button>
        <span class="md-error"></span>
      </md-field>

      <!---->
      <md-field>
        <label></label>
        <md-input>
        </md-input>
      </md-field>

      <!---->
      <md-field :class="$myapi.isPassword(form.password)&&form.password">
        <label></label>
        <md-input type="password" v-model="form.password">
        </md-input>
        <span class="md-error">6</span>
      </md-field>

    </md-card-content>

    <!---->
    <md-card-actions>
      <md-button class="my-raised my-hover" style="background-color: white;color: black" to="/login">
      </md-button>
      <md-button class="my-raised my-hover" style="background-color: white;color: black" v-on:click="register">
      </md-button>
    </md-card-actions>

  </md-card>
</template>

<script>
  export default {
    name: "Register",
    data() {
      return {
        // 
        words: ['<b style="color:orange">~~~</b>'],
        // 
        form: {
          password: '',
          phoneNumber: '',
        },
      }
    },
    methods: {
      isPhone(phone_number) {
        if (phone_number)
          return this.$myapi.isPhone(phone_number) ? '' : 'md-invalid'
        else return ''
      },
      isNull(password) {
        if (password)
          return this.$myapi.isPassword(password) ? '' : 'md-invalid'
        else return ''
      },
      register() {
        let _this = this;
        if (_this.$myapi.isPhone(_this.form.phone_number) && !_this.$myapi.isPassword(_this.form.password)) {
          this.$myapi.post('/user/add', _this.form, function (res) {
            _this.$alert.show(100, "+")
            sessionStorage.setItem("token", res.token)
            _this.$router.push("/register-more")
          })
        } else {
          _this.$alert.show("999", "")
        }
      }
    },
    created() {
      this.$store.commit("setWords", ['<b style="color:black"></b>', '<b style="color: orange"></b>'])
    }
  }
</script>

<style scoped>
  .my-hover:hover {
    cursor: url('https://www.mmgal.com/wp-content/themes/mygalgame/ui/images/mouse2.cur'), auto !important;
    background-color: rgba(255, 255, 255, .8);
  }

  .my-raised {
    background-color: rgba(10, 10, 0, .1);
    border-radius: 7px;
  }
</style>



