<template>
  <md-card class="md-elevation-5 my-hover">
    <md-card-content v-if="!nextFlag">


      <md-field :class="$myapi.isNull(form.userName)">
        <label></label>
        <md-input v-model="form.userName"></md-input>
        <span class="md-error"></span>
      </md-field>


      <md-field>
        <label></label>
        <md-file @change="update" accept="image/*" v-model="form.photoUrl"/>
      </md-field>

      <md-field :class="$myapi.isNull(form.identityCard)||$myapi.isIdCard(form.identityCard)">
        <label></label>
        <md-input v-model="form.identityCard"></md-input>
        <span class="md-error" v-if="$myapi.isNull(form.identityCard)"></span>
        <span class="md-error" v-if="$myapi.isIdCard(form.identityCard)&&form.identityCard"></span>
      </md-field>


      <md-field :class="$myapi.isNull(form.name)">
        <label></label>
        <md-input v-model="form.name"></md-input>
        <span class="md-error"></span>
      </md-field>

      <md-field v-if="!$store.state.isLogin" :class="$myapi.isNull(form.bankCardNumber)">
        <label></label>
        <md-input v-model="form.bankCardNumber"></md-input>
        <span class="md-error"></span>
      </md-field>


      <md-field v-if="!$store.state.isLogin" :class="isPhone(phone_number)||$myapi.isNull(phone_number)">
        <label></label>
        <md-input type="number" v-model="phone_number">
        </md-input>
        <md-button class="my-raised my-hover"></md-button>
        <span class="md-error" v-if="isPhone(phone_number)"></span>
        <span class="md-error" v-if="$myapi.isNull(phone_number)"></span>
      </md-field>

      <md-field v-if="!$store.state.isLogin">
        <label></label>
        <md-input>
        </md-input>
      </md-field>


      <md-field v-if="!$store.state.isLogin"
                :class="$myapi.isNull(form.payPassword)||$myapi.isPayPassword(form.payPassword)">
        <label></label>
        <md-input type="password" v-model="form.payPassword"></md-input>
        <span class="md-error" v-if="$myapi.isNull(form.payPassword)"> </span>
        <span class="md-error" v-if="$myapi.isPayPassword(form.payPassword)&&form.payPassword"> 6</span>
      </md-field>


      <md-field>
        <label></label>
        <md-textarea v-model="form.autograph"></md-textarea>
      </md-field>

      <md-field style="width: 30%;left: 0">
        <label></label>
        <md-select v-model="form.showMe">
          <md-option value="1"></md-option>
          <md-option value="0"></md-option>
        </md-select>
      </md-field>
    </md-card-content>

    <md-card-content v-else>
      <div>
        <p>1.</p>
        <p>
          <md-radio :value="0" v-model="form.questionResult[0]"></md-radio>
          <md-radio v-model="form.questionResult[0]" value="1"></md-radio>
          <md-radio v-model="form.questionResult[0]" value="2"></md-radio>
        </p>
      </div>
      <div>
        <p>2.</p>
        <p>
          <md-radio :value="0" v-model="form.questionResult[1]"></md-radio>
          <md-radio v-model="form.questionResult[1]" value="1"></md-radio>
          <md-radio v-model="form.questionResult[1]" value="2"></md-radio>
          <md-radio v-model="form.questionResult[1]" value="3"></md-radio>
        </p>
      </div>

      <div>
        <p>3.</p>
        <p>
          <md-radio :value="0" v-model="form.questionResult[2]"></md-radio>
          <md-radio v-model="form.questionResult[2]" value="1"></md-radio>
          <md-radio v-model="form.questionResult[2]" value="2"></md-radio>
        </p>
      </div>
      <div>
        <p>4.</p>
        <p>
          <md-radio :value="0" v-model="form.questionResult[3]"></md-radio>
          <md-radio v-model="form.questionResult[3]" value="1"></md-radio>
          <md-radio v-model="form.questionResult[3]" value="2"></md-radio>
        </p>
      </div>
      <div>
        <p>5.</p>
        <p>
          <md-radio :value="0" v-model="form.questionResult[4]"></md-radio>
          <md-radio v-model="form.questionResult[4]" value="1"></md-radio>
          <md-radio v-model="form.questionResult[4]" value="2"></md-radio>
          <md-radio v-model="form.questionResult[4]" value="3"></md-radio>
        </p>
      </div>
      <div>
        <p>6.</p>
        <p>
          <md-radio :value="0" v-model="form.questionResult[5]"></md-radio>
          <md-radio v-model="form.questionResult[5]" value="1"></md-radio>
          <md-radio v-model="form.questionResult[5]" value="2"></md-radio>
          <md-radio v-model="form.questionResult[5]" value="3"></md-radio>
        </p>
      </div>
      <div>
        <p>7.</p>
        <p>
          <md-radio :value="0" v-model="form.questionResult[6]"></md-radio>
          <md-radio v-model="form.questionResult[6]" value="1"></md-radio>
          <md-radio v-model="form.questionResult[6]" value="2"></md-radio>
          <md-radio v-model="form.questionResult[6]" value="3"></md-radio>
        </p>
      </div>

    </md-card-content>


    <md-card-actions>
      <md-button class="my-raised my-hover" v-if="$store.state.isLogin" to="/forget-paypsw"></md-button>
      <md-button class="my-raised my-hover" v-if="$store.state.isLogin" v-on:click="update_user_info"></md-button>
      <md-button class="my-raised my-hover" v-else-if="!nextFlag" v-on:click="next"></md-button>
      <md-button class="my-raised my-hover" v-else-if="nextFlag" v-on:click="nextFlag=false"></md-button>
      <md-button class="my-raised my-hover" v-if="nextFlag" v-on:click="save"></md-button>
    </md-card-actions>
  </md-card>
</template>

<script>
  export default {
    name: "Edit",
    data() {
      return {
        age_temp: [],
        nextFlag: false,
        config: {
          headers: {'Content-Type': 'multipart/form-data'}
        },
        phone_number: null,
        file: null,
        form: {
          name: null,
          photoUrl: null,
          identityCard: null,
          autograph: null,
          questionResult: [0, 0, 0, 0, 0, 0, 0],
          bankCardNumber: null,
          payPassword: null,
          showMe: 1,
          userName: null,
          chunk: '0',
        }
      }
    },
    computed: {},
    created() {
      if (this.$store.state.isLogin) {
        this.getMe()
      }
      for (let i = 1; i < 130; i++) {
        this.age_temp.push(i)
      }
    },
    methods: {
      next(){
        if(this.isForm()){
          this.nextFlag=true
        }
      },
      isForm() {
        return !(this.$myapi.isNull(this.form.userName) || this.$myapi.isNull(this.form.identityCard) || this.$myapi.isIdCard(this.form.identityCard) || this.$myapi.isNull(this.form.name) || this.$myapi.isNull(this.form.bankCardNumber) || this.isPhone(this.phone_number) || this.$myapi.isNull(this.phone_number) || this.$myapi.isNull(this.form.payPassword) || this.$myapi.isPayPassword(this.form.payPassword))
      },
      isPhone(phone_number) {
        if (phone_number)
          return this.$myapi.isPhone(phone_number) ? '' : 'md-invalid';
        else return ''
      },
      update(e) {
        this.file = new FormData();
        this.file.append('file', e.target.files[0])
      },
      getMe() {
        let _this = this;
        _this.$myapi.get("/user_info/me", {}, function (res) {
          _this.form = res.data;
          _this.form.showMe = _this.form.showMe ? 1 : 0
        })
      },
      save() {
        let _this = this;
        let temp = '';
        for (let i = 0; i < _this.form.questionResult.length - 1; i++) {
          temp += _this.form.questionResult[i] + '-'
        }
        temp += _this.form.questionResult[_this.form.questionResult.length - 1];
        _this.form.questionResult = temp;
        this.$myapi.post('/user_info/add', _this.form, function (res) {
          _this.$store.commit("login");
          _this.$alert.show(100, "+");
          _this.update_photo();
          _this.$router.push("/")
        })
      },
      update_user_info() {
        let _this = this;
        this.$myapi.post('/user_info/update', _this.form, function (res) {
          _this.$alert.show(100, "");
          _this.update_photo()
        })
      },
      update_photo() {
        let _this = this;
        let config = {
          headers: {
            'Content-Type': 'multipart/form-data'
          }
        };
        this.$axios.post(global.IP + '/user_info/upload?token=' + sessionStorage.getItem("token"), _this.file, config).then(function (res) {
          if (res.data.code == 100) {
          } else {
            _this.$alert.show(res.data.code, res.data.msg + "(" + _this.form.phoneNumber + ")")
          }
        })
      }
    }
  }
</script>

<style scoped>

</style>



