export default {
  state: {
    /**
     *
     */
    info: {
      age: "",
      autograph: "",
      bankCardNumber: null,
      identityCard: null,
      name: null,
      photoUrl: 'http://wx3.sinaimg.cn/mw690/006okS32ly8fla2yv1mowj30dz0e8t9n.jpg',
      questionResult: null,
      sex: null,
      showMe: false,
      userId: null,
      userName: ''
    },
    isGetInfo:false,
  },
  getters: {
  },
  mutations:{
    /**
     * userInfo
     * @param state
     * @param userInfo
     */
    setUserInfo: function (state, userInfo) {
      state.info = userInfo
      state.isGetInfo = true
    },
    /**
     * userInfo
     */
    resetUserInfo: function (state) {
      state.info = {
        age: "",
        autograph: "",
        bankCardNumber: null,
        identityCard: null,
        name: null,
        photoUrl: '/static/default.jpg',
        questionResult: null,
        sex: null,
        showMe: "",
        userId: null,
        userName: '',
      }
      state.isGetInfo = false
    }
  }
}



