<template>
  <div>


    <!--<SmallPerson :words="words" :duration="10000"/>-->
    <md-dialog :md-active.sync="sell_flag" class="md-elevation-5 my-hover">
      <md-dialog-content>
        <!--<img :src="buy_info.url" style="width: 400px"/>-->
        <!--<p style="border-left: 10px solid rgba(10,10,0,0.7)">{{buy_info.name}}({{buy_info.fund_id}})</p>-->
        <!--<p style="border-left: 10px solid rgba(10,10,0,0.7)">{{buy_info.buying_rate*100}}%</p>-->
        <!--<p style="border-left: 10px solid rgba(135,206,250,0.6)">xx</p>-->
        <!--<p style="border-left: 10px solid rgba(10,10,0,0.7)">{{buy_info.thistime}}</p>-->
        <p><b></b>{{sell_form.fundName}}({{sell_form.fundId}})</p>

        <md-field>
          <label>({{sell_form.thisFundShare}})</label>
          <md-input v-model="sell_form.sellShare"></md-input>
        </md-field>

        <md-field>
          <label></label>
          <md-input v-model="sell_form.payPassword"></md-input>
        </md-field>
        <p>
          150510()0513().00%~1.50%(7.50%)
        </p>
        <md-card-actions>
          <md-button class="my-raised my-hover" v-on:click="sell_click"></md-button>
          <md-button class="my-raised my-hover" v-on:click="sell_flag=false"></md-button>
        </md-card-actions>

      </md-dialog-content>
    </md-dialog>
    <div class="md-layout md-gutter md-alignment-top-center">


      <div class="md-layout-item md-large-size-50 md-medium-size-40 md-small-size-100 md-xsmall-size-100">


        <md-card class="my-hover">
          <md-card-content id="top">

            <p style="font-size: 20px;text-align: center;color: rgba(0,0,0,1)"></p>
            <!--<p style="font-size: 20px;text-align: center;color: blue" v-if="!$store.state.isLogin">(<a to="/login"></a>)</p>-->

            <div>
              <div class="md-layout md-gutter md-alignment-top-center"
                   style="margin-top: 10px;border-bottom: 2px solid rgba(10,10,0,0.7) ">
                <div class="md-layout-item md-large-size-30 md-medium-size-30 md-small-size-30 md-xsmall-size-30"
                     style="border-right: 2px solid rgba(10,10,0,0.7)">
                  <p></p>
                  <p style="font-size: 18px;color: black">{{myMoney}}</p>
                </div>
                <div class="md-layout-item md-large-size-30 md-medium-size-30 md-small-size-30 md-xsmall-size-30"
                     style="border-right: 2px solid rgba(10,10,0,0.7)">
                  <p></p>
                  <p style="font-size: 18px;color: black" >{{yestodayGetMoney.toFixed(2)}}</p>

                </div>
                <div class="md-layout-item md-large-size-30 md-medium-size-30 md-small-size-30 md-xsmall-size-30">
                  <p></p>
                  <p style="color: red;font-size: 18px">{{(rate*100-100).toFixed(2)}}%</p>
                </div>
              </div>
              <md-tabs class="md-transparent" md-alignment="fixed">
                <md-tab id="x" md-label="" v-on:click="show_user_rank"></md-tab>

                <md-tab id="y" md-label="" v-on:click="show_fund_rank"></md-tab>
                1
              </md-tabs>


              <div>

                <md-card-actions v-show="!user_flag">
                  <md-radio :value="true" @change="show_buy(1)" v-model="select_buy_flag"></md-radio>
                  <md-radio :value="false" @change="show_sell(1)" v-model="select_buy_flag"></md-radio>
                  <p style="color: red;font-size: 20px;"></p>
                </md-card-actions>

                <template v-if="!funds.length">
                  <p style="color: rgba(0,0,0,1);font-size: 25px;text-align: right">
                    {{select_buy_flag?"omg,":"omg,"}}</p>
                </template>
                <template v-else v-for="(fund,index) in funds">
                  <div class="md-layout md-gutter md-alignment-top-center"
                       style="margin-top: 10px;border-bottom: 5px solid rgba(135,206,250,0.6) ">
                    <div class="md-layout-item md-large-size-30 md-medium-size-30 md-small-size-30 md-xsmall-size-30">
                      <img :src="fund.photo" style="width: 100%"/>
                    </div>
                    <div class="md-layout-item md-large-size-30 md-medium-size-30 md-small-size-30 md-xsmall-size-30">
                      <p>:<b>{{fund.fundName}}</b></p>
                      <p>id:<b>{{fund.fundId}}</b></p>
                      <p v-show="fund.sureNet>0"><b>{{fund.sureNet}}</b></p>
                      <p v-show="fund.serviceCharge>=0&&fund.sureNet>0"><b>{{fund.serviceCharge}}</b></p>
                    </div>
                    <div class="md-layout-item md-large-size-30 md-medium-size-30 md-small-size-30 md-xsmall-size-30">
                      <p v-show="fund.timeBuying"><b>{{ $myapi.parseMyDate(fund.timeBuying)}}</b></p>
                      <p v-show="fund.sellTime"><b>{{ $myapi.parseMyDate(fund.sellTime)}}</b></p>
                      <p v-show="fund.transactionAmount"><b>{{fund.transactionAmount}}</b></p>
                      <p v-show="!user_flag&&!fund.sellTime"><b style="color: red">{{fund.confirmSign?"":""}}</b>
                      </p>
                      <p v-show="!user_flag&&fund.sellTime"><b style="color: red">{{fund.confirmSign?"":""}}</b>
                      </p>
                      <p v-show="fund.sellShare&&!user_flag"><b>{{fund.sellShare}}</b></p>

                      <p v-show="fund.allMoney"><b>{{fund.allMoney}}</b></p>
                      <p style="color: red" v-show="fund.thisFundShare*fund.historyPrice">
                        {{(fund.thisFundShare*fund.historyPrice).toFixed(2)}}</p>
                      <p style="color: red" v-show="fund.thisFundShare*fund.willMoney">
                        {{(fund.thisFundShare*fund.willMoney).toFixed(2)}}</p>

                      <md-card-actions>
                        <md-button class="my-raised my-hover" v-on:click="sell(index)" v-show="user_flag"></md-button>
                        <md-button :to="'/fund/' + fund.fundId" class="my-raised"></md-button>
                      </md-card-actions>

                    </div>
                  </div>
                </template>
                <md-card-actions>
                  <md-field :class="$myapi.isMoreThanLastPage(page,end_page)">
                    <label>{{page}}/{{end_page}}</label>
                    <md-input v-model="page" type="number"></md-input>
                    <span class="md-error"></span>
                  </md-field>
                  <md-button class="my-hover my-raised" v-on:click="show_buy(page)"></md-button>
                  <md-button class="my-raised my-hover" v-on:click="pre" v-show="page!=1"></md-button>
                  <md-button class="my-raised my-hover" v-on:click="next" v-show="end_page!=page"></md-button>
                </md-card-actions>

              </div>
            </div>
          </md-card-content>
        </md-card>
      </div>


    </div>
  </div>
</template>

<script>
  import SmallPerson from './SmallPerson'

  export default {
    name: 'HelloWorld',
    components: {
      SmallPerson
    },
    data() {
      return {
        yestodayGetMoney: 0,
        select_buy_flag: true,
        select_see_date: 30,
        user_flag: true,
        fund_flag: false,
        // 
        sell_flag: false,
        words: ['<b style="color: rgba(0,0,0,1)"></b>'],
        funds: [],
        page: 1,
        myMoney: 0,
        end_page: 1,
        rate: 0,
        sell_form: {
          sellShare: 0,
          payPassword: null
        }
      }
    },
    methods: {
      // 
      getMyRate() {
        let _this = this;
        _this.$myapi.get("/user_info/me/get_rate", {}, function (res) {
          _this.rate = res.data.rate
        })
      },
      //
      sell(index) {
        let _this = this;
        _this.sell_flag = true;
        _this.sell_form = _this.funds[index];
        _this.sell_form['sellShare'] = _this.funds[index].thisFundShare
      },
      //
      sell_click() {
        let _this = this;
        _this.$myapi.post('/sell_order/add', _this.sell_form, function (res) {
          _this.$alert.show("100", "");
          _this.show_has_fund(1);
        })
        _this.sell_flag = false
      },
      show_user_rank() {
        this.user_flag = true;
        this.fund_flag = false;
        this.select_buy_flag = false;
        this.show_has_fund(1)
      },
      show_has_fund(page) {
        let _this = this;
        _this.$loading.show();
        this.$myapi.get("/buy_order/show_has_funds/" + page, {}, function (res) {
          if (res.data != null) {
            _this.funds = res.data.list.list;
            _this.yestodayGetMoney = res.data.yestodayGetMoney;
            if(!_this.yestodayGetMoney){
              _this.yestodayGetMoney = 0
            }
            _this.end_page = res.data.list.lastPage;
            _this.$myapi.returnTop("top")
          }
          _this.$loading.hide()
        })
      },
      show_fund_rank() {
        this.select_buy_flag = true;
        this.user_flag = false;
        this.fund_flag = true;
        this.show_buy(1)
      },
      show_buy(page) {
        if(this.$myapi.isMoreThanLastPage(this.page,this.end_page)){
          return
        }
        let _this = this;
        _this.page = page;
        _this.$loading.show();
        this.$myapi.get("/buy_order/show_buy/" + page, {}, function (res) {
          if (res.data != null) {
            _this.funds = res.data.list;
            _this.end_page = res.data.lastPage;
            _this.$myapi.returnTop("top")
          }
          _this.$loading.hide()
        })
      },
      show_sell(page) {
        let _this = this;
        this.$myapi.get("/sell_order/show_sell/" + page, {}, function (res) {
          if (res.data.list != null)
            _this.funds = res.data.list;
          _this.end_page = res.data.lastPage
          _this.$myapi.returnTop("top")
        })
      },
      show_buy_amount() {
        let _this = this;
        _this.$loading.show();
        this.$myapi.get("/buy_order/show/buy_amount", {}, function (res) {
          _this.myMoney = res.data.getAllAmount;
          _this.$loading.hide()
        })
      },
      next() {
        this.page = parseInt(this.page) + 1;
        if (this.fund_flag && this.select_buy_flag) {
          this.show_buy(this.page)
        } else if (this.fund_flag && !this.select_buy_flag) {
          this.show_sell(this.page)
        }
      },
      pre() {
        this.page = parseInt(this.page) - 1;
        if (this.fund_flag && this.select_buy_flag) {
          this.show_buy(this.page)
        } else if (this.fund_flag && !this.select_buy_flag) {
          this.show_sell(this.page)
        }
      }
    },
    created() {
      if (this.$store.state.isLogin) {
        this.show_buy_amount();
        this.show_has_fund(1);
        this.getMyRate()
      }
      this.$store.commit("setWords", this.words)

      // this.show_buy()
      // this.init_weibo()
      // this.isLoginIn()
    },
    mounted() {
    }
  }
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
  .my-raised {
    background-color: rgba(10, 10, 0, .1);
    border-radius: 7px;
  }

  .my-hover:hover {
    cursor: url('https://www.mmgal.com/wp-content/themes/mygalgame/ui/images/mouse2.cur'), auto !important;
    background-color: rgba(255, 255, 255, .8);
  }

  h1, h2 {
    font-weight: normal;
  }

  ul {
    list-style-type: none;
    padding: 0;
  }

  li {
    display: inline-block;
    margin: 0 10px;
  }

  a {
    color: #42b983;
  }

  .md-card {
    margin-top: 20px;
    margin-bottom: 20px;
    border-radius: 5px;
  }


</style>



